.header {
  display: flex;
  justify-content: space-between;
  border: 1px solid black;
}

.logo {
  width: 200px;
}

.nav-items ul {
  font-size: 24px;
  display: flex;
  list-style-type: none;
}

.nav-items ul li {
  padding: 10px;
  margin: 10px;
}

.res-card {
  padding: 5px;
  width: 200px;
  border: 1px solid black;
  background-color: #f0f0f0;
  margin: 10px;
  cursor: pointer;
}

.search {
  padding: 10px;
}

.res-logo {
  width: 190px;
}

.res-container {
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
}

.filter-btn {
  margin: 15px;
}

.shimmer-container {
  display: flex;
  flex-wrap: wrap;
}
.shimmer-card {
  width: 200px;
  height: 400px;
  margin: 20px;
  background-color: #f0f0f0;
}

.login {
  padding: 0 20px;
  margin: 0 20px;
  cursor: pointer;
}

.filter {
  display: flex;
  flex-wrap: wrap;
  padding: 20px;
}
